export default function CatalogPage() {
  const [products, setProducts] = useState(initialProducts);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);
  const [hasMore, setHasMore] = useState(true);
  const observerTarget = useRef<HTMLDivElement>(null);

  // Simulate loading more products
  const loadMoreProducts = async () => {
    if (loading || !hasMore) return;
    
    setLoading(true);
    
    // Simulate API call delay
    await new Promise(resolve => setTimeout(resolve, 1000));
    
    // Generate more products (in real app, fetch from API)
    const newProducts = initialProducts.map((product, idx) => ({
      ...product,
      id: products.length + idx + 1,
    }));
    
    setProducts(prev => [...prev, ...newProducts]);
    setPage(prev => prev + 1);
    setLoading(false);
    
    // Stop after 4 pages for demo
    if (page >= 3) {
      setHasMore(false);
    }
  };

  // Intersection Observer for infinite scroll
  useEffect(() => {
    const observer = new IntersectionObserver(
      entries => {
        if (entries[0].isIntersecting && hasMore && !loading) {
          loadMoreProducts();
        }
      },
      { threshold: 0.1 }
    );

    const currentTarget = observerTarget.current;
    if (currentTarget) {
      observer.observe(currentTarget);
    }

    return () => {
      if (currentTarget) {
        observer.unobserve(currentTarget);
      }
    };
  }, [hasMore, loading, page]);

  return (
    <main className="mx-auto w-full max-w-7xl px-4 sm:px-6 lg:px-8 py-8">
      {/* Breadcrumb */}
      <nav className="flex items-center gap-2 mb-8">
        <Link href="/" className="text-[10px] font-bold text-neutral-500 hover:text-black">
          Home
        </Link>
        <span className="text-[10px] font-bold text-neutral-500">/</span>
        <span className="text-[10px] font-bold text-black">Shop</span>
      </nav>

      {/* Header Section */}
      <div className="mb-4">
        <h1 className="text-3xl font-bold mb-1">More You, Less Noise.</h1>
        <p className="text-sm text-neutral-700">
          Discover timeless forms made with intention for the way you move, and the way you feel.
        </p>
      </div>

      {/* Filter Bar */}
      <div className="flex items-center justify-between mb-6">
        <button className="flex items-center gap-2 text-[10px] hover:opacity-70 transition-opacity">
          <Filter className="w-3 h-3" />
          Фильтр
        </button>
        <p className="text-[10px] text-neutral-700">{products.length} Products</p>
      </div>

      {/* Product Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-x-2 gap-y-12 mb-8">
        {products.map((product) => (
          <ProductCard
            key={product.id}
            href={product.href}
            imageSrc={product.imageSrc}
            imageAlt={product.imageAlt}
            title={product.title}
            sizeLabel={product.sizeLabel}
            priceFormatted={product.priceFormatted}
            compareAtFormatted={product.compareAtFormatted}
            rating={product.rating}
            className="w-full"
          />
        ))}
      </div>

      {/* Loading indicator */}
      {loading && (
        <div className="flex justify-center py-8">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-black"></div>
        </div>
      )}

      {/* Intersection observer target */}
      <div ref={observerTarget} className="h-10" />

      {/* End message */}
      {!hasMore && (
        <p className="text-center text-sm text-neutral-500 py-8">
          Вы просмотрели все товары
        </p>
      )}
    </main>
  );
}